<template>
  <div class="login-page">
    <van-nav-bar title="面经注册" />

    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[
          { required: true, message: '请填写用户名' },
          { pattern: /^\w{5,}$/, message: '用户名至少包含5个字符' }     //正则
        ]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[
          { required: true, message: '请填写密码' },
          { pattern: /^\w{6,}$/, message: '密码至少包含6个字符' }     //正则
        ]"
      />
      <div style="margin: 16px">
        <van-button block type="primary" native-type="submit"
          >注册</van-button
        >
      </div>
    </van-form>
    <router-link class="link" to="/login">有账号，去登录</router-link>
  </div>
</template>

<script>
// import { Toast } from 'vant'
import { register } from '@/api/user'
export default {
  name: 'register-page',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    async onSubmit (values) {
      // 发送注册的请求
      // try catch 尝试执行 try 代码块的逻辑，如果有错误，会被catch捕获，存入err中
      try {
        await register(values)
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 1000
        })
        setTimeout(() => {
          this.$toast.success('注册成功')
        }, 1000)
        setTimeout(() => {
          this.$router.push('/login')
        }, 3000)
      } catch (err) {
        console.dir(err)
        // 每次都要自己写一下，错误提示 => 统一在响应拦截器中，进行错误提示
        // this.$toast(err.response.data.message)
      }
    }
  }
}

// Toast('提示内容')
/*  Toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })
      setTimeout(() => {
        console.log('3秒后请求回来了')
        // Toast.clear()
        Toast.success('注册成功')
      }, 3000) */

// 发送注册的请求
// this.$toast.fail('注册失败')
</script>

<style lang="less" scoped>
.link {
  color: #069;
  font-size: 12px;
  padding-right: 20px;
  float: right;
}
</style>
